import { Box, Fab, Button } from '@mui/material';
import { Link } from 'react-router-dom';
import ThemeToggle from './ThemeToggle';
import logo from '../images/testLogo.png'

const Navbar = () => {

  const navbarItems = [
    { path: "/jobs", label: "Jobs" },
    { path: "/postajob", label: "Post a Job" },
    { path: "/aboutus", label: "About Us" },
    { path: "/contact", label: "Contact Us" },
    { path: "/konto", label: "Login" },
  ];

  return ( 
    <Box sx={{ display:"flex", flexDirection:"row", justifyContent:"center", alignItems:"center", padding:"2vh 0", borderBottom:"1px solid gray"}}>
      <Box component={Link} to="/" sx={{ display: "inline-block", width: "10%", marginRight:10 }}>
        <img src={logo} style={{ width: "100%", display: "block" }} alt="logo"/>
      </Box>
  
      {navbarItems.map((item, index) => (
        <Fab key={index} component={Link} to={item.path} size='medium' variant='extended' color='primary' aria-label="add" sx={{marginRight:2, fontSize:13, padding:2, borderRadius:2}}>
          {item.label}
        </Fab>
      ))}
      <ThemeToggle />
    </Box>
  );
}

export default Navbar;
