import { createTheme } from '@mui/material/styles';

const getTheme = (themeType) => {
  return createTheme({
    palette: {
      mode: themeType,
      primary: {
        main: themeType === 'dark' ? '#fff' : '#808080', // Biały w dark mode, czarny w light mode
      },
    },
  });
};

export default getTheme;
